import React from "react";

const Label = ({ icon, iconType = "solid", text }) => {
  return (
    <div className="label label--primary-100">
      <i className={`fa-${iconType} fa-${icon} label__icon`}></i>
      <span>{text}</span>
    </div>
  );
};

export default Label;



