import React from "react";
import ProfilePassword from "../sections/profile-password.sections";
import ProfileContact from "../sections/profile-contact.section";
import ProfileUsername from "../sections/profile-username.section";

const ProfilePage = () => {
  return (
    <div className="register">
      <div className="container">
        <h1 className="h2 mb-1">Perfil</h1>

        <ProfileUsername />
        <ProfileContact />
        <ProfilePassword />
      </div>
    </div>
  );
};

export default ProfilePage;



