import React from "react";
import Label from "../components/label.component";

const DetailsInfo = ({ data }) => {
  console.log(data.location);
  return (
    <section className="details__info">
      <div className="details__info-items">
        <Label
          text={`${data.province},${data.neighborhood}`}
          icon="location-pin"
        />
        <Label text={data.contact_primary} icon="phone" />
        <Label text={data.contact_alternative} icon="phone" />
        <Label text={data.facebook} icon="facebook" iconType="brands" />
        <Label text={data.instagram} icon="instagram" iconType="brands" />
        <Label text={data.email} icon="at" />
        <Label text={data.whatsapp} icon="whatsapp" iconType="brands" />
      </div>
      {data.location && (
        <div>
          <div className="details__info-map"></div>
          <a
            href={`geo:${data.location}`}
            className="btn btn--primary-100 details__info-btn"
          >
            Ver no Mapa
          </a>
        </div>
      )}
    </section>
  );
};

export default DetailsInfo;



