import React, { useState } from "react";
import ReactDOM from "react-dom";
import Button from "./button.component";
import Loader from "./loader.component";
import Message from "./message.component";
import { useSelector } from "react-redux";
const Rating = ({ show, onClose, entity, entityId }) => {
  const [text, setText] = useState("");
  const [stars, setStars] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState("");
  const currentUser = useSelector((state) => state.user.currentUser);

  const onSubmitHandler = async (e) => {
    e.preventDefault();

    try {
      setIsLoading(true);

      const formData = new FormData();

      formData.append("text", text);
      formData.append("stars", stars);
      formData.append("entity", entity);
      formData.append("entityId", entityId);
      formData.append("userId", currentUser && currentUser.id);

      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/post-comment`,
        {
          method: "POST",
          headers: {
            Authorization: `bearer ${currentUser && currentUser.token}`,
          },
          body: formData,
        }
      );

      const data = await response.json();

      console.log(data);

      if (data.status === "success") {
        onClose();
      } else {
        setMessage(data.message);
      }
    } catch (error) {
      console.log(error.message);
    } finally {
      setIsLoading(false);
    }
  };
  const content = (
    <form
      className={`rating ${show ? "rating--show" : ""}`}
      onSubmit={onSubmitHandler}
    >
      <div className="rating__header mb-2">
        <h4 className="h4 rating__title">Deixe o seu coment&aacute;rio</h4>
        <div className="rating__close" onClick={onClose}>
          <i className="fa-solid fa-x"></i>
        </div>
      </div>
      {isLoading ? (
        <Loader />
      ) : (
        <div className="rating__body">
          <div className="rating__stars">
            <p className="p text-center">Quantas estrelas </p>
            <div className="rating__star-list">
              {[1, 2, 3, 4, 5].map((item) => {
                return (
                  <i
                    key={item}
                    onClick={() => setStars(item)}
                    className={`fa-solid fa-star ${
                      item <= stars ? "text-primary-100" : ""
                    }`}
                  ></i>
                );
              })}
            </div>
          </div>
          <p>Commentario</p>
          <textarea
            name="text"
            id="text"
            rows="4"
            className="rating__text"
            value={text}
            onChange={(e) => setText(e.target.value)}
          ></textarea>
        </div>
      )}
      {!isLoading && (
        <div className="rating__footer">
          <Button type="submit" classes="btn--primary-100 btn--block">
            Publicar
          </Button>
        </div>
      )}
    </form>
  );

  return ReactDOM.createPortal(content, document.getElementById("rating-hook"));
};

export default Rating;



