import React from "react";
import Label from "../components/label.component";

const DetailsDescription = ({ data }) => {
  return (
    <section className="details__description">
      <div className="flex flex-justify-space-between flex-align-center mb-1">
        <h4 className="h2">{data.name}</h4>
        <Label icon="star" text={data.stars} />
      </div>
      <p className="p p--justify">{data.description}</p>
    </section>
  );
};

export default DetailsDescription;



