import React from "react";
import { Link } from "react-router-dom";

const ProfileItem = ({ to, text, closeMenu, isVisible }) => {
  return (
    <li
      className={`profile-menu__item ${
        isVisible ? "profile-menu__item--show" : ""
      }`}
    >
      <Link to={to} className="profile-menu__link" onClick={closeMenu}>
        {text}
      </Link>
    </li>
  );
};

export default ProfileItem;



