import React from "react";
import ReactDOM from "react-dom";
import { Link } from "react-router-dom";

const Drawer = ({ show, closeDrawer }) => {
  const content = (
    <aside className={`drawer ${show ? "drawer--show" : ""}`}>
      <div>
        <Link to="/" className="drawer__title" onClick={closeDrawer}>
          Curte la
        </Link>
      </div>
      <ul className="drawer__menu">
        <li className="drawer__menu-item">
          <Link to="/bars" className="drawer__menu-link" onClick={closeDrawer}>
            <img
              src={require("../assets/icons/bars-icon.svg")}
              alt="Bares"
              className="drawer__menu-icon"
            />
            <span>Bares</span>
          </Link>
        </li>
        <li className="drawer__menu-item">
          <Link
            to="/restaurants"
            className="drawer__menu-link"
            onClick={closeDrawer}
          >
            <img
              src={require("../assets/icons/restaurants-icon.svg")}
              alt="Retaurantes"
              className="drawer__menu-icon"
            />
            <span>Restaurantes</span>
          </Link>
        </li>
        <li className="drawer__menu-item">
          <Link
            to="/bottle-stores"
            className="drawer__menu-link"
            onClick={closeDrawer}
          >
            <img
              src={require("../assets/icons/bottle-stores-icon.svg")}
              alt="Bottle Store"
              className="drawer__menu-icon"
            />
            <span>Bottle Stores</span>
          </Link>
        </li>
        <li className="drawer__menu-item">
          <Link
            to="/events"
            className="drawer__menu-link"
            onClick={closeDrawer}
          >
            <img
              src={require("../assets/icons/events-icon.svg")}
              alt="Eventos"
              className="drawer__menu-icon"
            />
            <span>Eventos</span>
          </Link>
        </li>
        <li className="drawer__menu-item">
          <Link to="/" className="drawer__menu-link" onClick={closeDrawer}>
            <img
              src={require("../assets/icons/favorites-icon.svg")}
              alt="Favoritos"
              className="drawer__menu-icon"
            />
            <span>Favoritos</span>
          </Link>
        </li>
        <li className="drawer__menu-item">
          <Link to="/" className="drawer__menu-link" onClick={closeDrawer}>
            <img
              src={require("../assets/icons/phone-icon.svg")}
              alt="Contactos"
              className="drawer__menu-icon"
            />
            <span>Contactos</span>
          </Link>
        </li>
      </ul>
      <p className="drawer__copyright">
        Curtela &copy; 2022 Todos direitos reservados
      </p>
    </aside>
  );
  return ReactDOM.createPortal(content, document.getElementById("drawer-hook"));
};

export default Drawer;



