import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import useFetch from "../hooks/useFetch.hook";
import Loader from "../components/loader.component";
import DetailsComment from "../sections/details-comment.section";
import DetailsDescription from "../sections/details-description.section";
import DetailsInfo from "../sections/details-info.section";
import DetailsImage from "../sections/details-image.section";
const DetailsPage = () => {
  const [data, setData] = useState(null);
  const [comments, setComments] = useState([]);
  const { entity, id } = useParams();

  const [selectedSection, setSelectedSection] = useState(3);

  const [results] = useFetch(
    `${process.env.REACT_APP_API_URL}/${entity}/${id}`
  );

  let detailsContent = <Loader />;

  if (selectedSection === 1) {
    detailsContent = <DetailsDescription data={data} />;
  }

  if (selectedSection === 2) {
    detailsContent = <DetailsInfo data={data} />;
  }

  if (selectedSection === 3) {
    detailsContent = (
      <DetailsComment comments={comments} entity={entity} entityId={id} />
    );
  }

  const selectContentHandler = (number) => {
    setSelectedSection(number);
  };

  useEffect(() => {
    if (results) {
      if (results.status === "success") {
        setData(results.data);

        setComments(results.comments);
      }
    }
  }, [results]);

  if (!data) {
    return <Loader />;
  }

  return (
    <div className="details">
      <div className="details__container">
        <div className="details__top">
          <div className="flex flex-justify-space-between flex-align-center mb-1">
            <h1>
              <span className="text-primary-100">{data.name}</span>
            </h1>
          </div>
          <hr className="mb-2" />
        </div>

        <div className="grid grid-xl-2 grid-lg-2 grid-md-1 grid-sm-1">
          <DetailsImage data={data} entity={entity} />
          <section className="details__content">
            <div className="details__header">
              <button
                type="button"
                className={`btn details__header-btn ${
                  selectedSection === 1 ? "details__header--active" : ""
                }`}
                onClick={() => selectContentHandler(1)}
              >
                Descri&ccedil;&atilde;o
              </button>
              <button
                type="button"
                className={`btn details__header-btn ${
                  selectedSection === 2 ? "details__header--active" : ""
                }`}
                onClick={() => selectContentHandler(2)}
              >
                Informa&ccedil;&otilde;es
              </button>
              <button
                type="button"
                onClick={() => selectContentHandler(3)}
                className={`btn details__header-btn ${
                  selectedSection === 3 ? "details__header--active" : ""
                }`}
              >
                Coment&aacute;rios
              </button>
            </div>
            <div className="details__content-body">{detailsContent}</div>
          </section>
        </div>
      </div>
    </div>
  );
};

export default DetailsPage;



