import React from "react";
import ReactDom from "react-dom";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

const Cover = ({ show }) => {
  const content = (
    <div className={`cover ${show ? "cover--show" : ""}`}>
      <Link className="cover__text" to="/activate-account">
        Ver mais
      </Link>
    </div>
  );
  return ReactDom.createPortal(content, document.getElementById("cover-hook"));
};

export default Cover;



