import React from "react";
import ReactDOM from "react-dom";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import ProfileItem from "./profile-item.component";
import { setCurrentUser } from "./../redux/actions/user.actions";
import { showAddToHomeScreen } from "../utils/pwa";

const ProfileMenu = ({ show, closeMenu }) => {
  const currentUser = useSelector((state) => state.user.currentUser);

  const dispatch = useDispatch();

  const onLogoutHandler = () => {
    dispatch(setCurrentUser(null));

    closeMenu();
  };

  const content = (
    <ul className={`profile-menu ${show && "profile-menu--show"}`}>
      <li className={`profile-menu__item profile-menu__item--show`}>
        <div className="profile-menu__link" onClick={showAddToHomeScreen}>
          Adicionar App
        </div>
      </li>
      <ProfileItem
        to="/login"
        text="Entrar"
        closeMenu={closeMenu}
        isVisible={currentUser === null}
      />
      <ProfileItem
        to="/register"
        text="Registar"
        closeMenu={closeMenu}
        isVisible={currentUser === null}
      />
      <ProfileItem
        to="/profile"
        text="Perfil"
        closeMenu={closeMenu}
        isVisible={currentUser !== null}
      />
      <ProfileItem
        to="/"
        text="Definicoes"
        closeMenu={closeMenu}
        isVisible={currentUser !== null}
      />
      <ProfileItem
        to="/"
        text="Sair"
        closeMenu={onLogoutHandler}
        isVisible={currentUser !== null}
      />
    </ul>
  );
  return ReactDOM.createPortal(
    content,
    document.getElementById("profile-menu-hook")
  );
};

export default ProfileMenu;



