import { useEffect, useState } from "react";

const useFetch = (url) => {
  const [data, setData] = useState(null);
  const [error, setError] = useState(null);
  useEffect(() => {
    (async function () {
      try {
        const response = await fetch(url);

        const results = await response.json();

        setData(results);
      } catch (error) {
        setError(error);
      }
    })();
  }, [url]);

  return [data, error];
};

export default useFetch;



