import React, { useState } from "react";
import Loader from "./../components/loader.component";

const DetailsImage = ({ data, entity }) => {
  const [selectedImage, setSelectedImage] = useState(0);
  const images = [data.image_cover];
  if (data.image_1) {
    images.push(data.image_1);
  }
  if (data.image_2) {
    images.push(data.image_2);
  }
  if (data.image_3) {
    images.push(data.image_3);
  }
  if (data.image_4) {
    images.push(data.image_4);
  }
  if (data.image_5) {
    images.push(data.image_5);
  }

  const selectImageHandler = (index) => {
    setSelectedImage(index);
  };

  const selectNextImageHandler = (direction) => {
    if (selectedImage <= images.length - 2) {
      setSelectedImage((currentIndex) => currentIndex + 1);
      console.log("Increase");
    } else {
      setSelectedImage(0);
    }
  };

  const selectPreviousImageHandler = (direction) => {
    if (selectedImage > 0) {
      setSelectedImage((currentIndex) => currentIndex - 1);
    } else {
      setSelectedImage(images.length - 1);
    }
  };

  return (
    <section className="details__image">
      {images.length === 0 && <Loader />}
      {images.length > 0 && (
        <div className="preview">
          <div
            className="preview__main"
            style={{
              backgroundImage: `url("${process.env.REACT_APP_ASSET_URL}/${entity}/${images[selectedImage]}")`,
            }}
          ></div>
          <div className="preview__controls">
            <button
              className="btn preview__controls-btn preview__controls-btn--left"
              onClick={selectPreviousImageHandler}
            >
              <i className="fa-solid fa-arrow-left"></i>
            </button>
            <button
              className="btn preview__controls-btn preview__controls-btn--right"
              onClick={selectNextImageHandler}
            >
              <i className="fa-solid fa-arrow-right"></i>
            </button>
          </div>
          <div className="preview__dots">
            {images.map((image, index) => {
              return (
                <div
                  key={index}
                  className={`preview__dots-item ${
                    selectedImage === index && "preview__dots-item--active"
                  }`}
                  onClick={() => selectImageHandler(index)}
                ></div>
              );
            })}
          </div>
        </div>
      )}
    </section>
  );
};

export default DetailsImage;



