import React, { useState } from "react";
import Comment from "../components/comment.component";
import { useSelector } from "react-redux";
import Button from "../components/button.component";
import Rating from "../components/rating.component";
import Overlay from "../components/overlay.component";
const DetailsComment = ({ comments, entity, entityId }) => {
  const [openRating, setOpenRating] = useState(false);

  const onOpenRatingHandler = () => {
    setOpenRating(true);
  };

  const onCloseRatingHandler = () => {
    setOpenRating(false);
  };

  return (
    <React.Fragment>
      <Rating
        show={openRating}
        onClose={onCloseRatingHandler}
        onSubmit={() => {}}
        entity={entity}
        entityId={entityId}
      />
      {openRating && <Overlay onClick={onCloseRatingHandler} />}
      <section className="details__comments">
        <div className="flex flex-justify-space-between flex-align-center mb-1">
          <h4 className="h2">
            Comentarios <sub>({comments.length})</sub>
          </h4>
          <div className="label label--primary-100">
            <i className="fa-solid fa-star label__icon"></i>
            <span>4.5</span>
          </div>
        </div>

        <div className="comment__list">
          {comments.map((comment) => {
            return <Comment key={comment.id} comment={comment} />;
          })}
        </div>

        <Button
          type="button"
          classes="btn--primary-100  comment__btn"
          onClick={onOpenRatingHandler}
        >
          Commentar
        </Button>
      </section>
    </React.Fragment>
  );
};

export default DetailsComment;



