import React, { useState, useEffect } from "react";
import Card2 from "../components/card2.component";
import useFetch from "../hooks/useFetch.hook";
import Loader from "../components/loader.component";

const EventsPage = () => {
  const [data, setData] = useState([]);
  const [result] = useFetch(`${process.env.REACT_APP_API_URL}/events`);
  useEffect(() => {
    if (result) {
      if (result.status === "success") setData(result.data);
    }
  }, [result]);
  return (
    <section className="events">
      <div className="container">
        <div className="flex flex-justify-space-between flex-align-center mb-1">
          <h1>
            Todos <span className="text-primary-100">Eventos</span>
          </h1>
          <button className="btn">
            <i className="fa-solid fa-filter"></i>
          </button>
        </div>
        <hr className="mb-2" />

        {data.length === 0 && <Loader />}
        <section className="grid grid-xl-3 grid-lg-2 grid-md-2 grid-sm-1 grid-gap-1">
          {data.length > 0 &&
            data.map((element, index) => {
              return (
                <div className="grid-item" key={element.id}>
                  <Card2 entity="bars" data={element} type="horizontal" />
                </div>
              );
            })}
        </section>
      </div>
    </section>
  );
};

export default EventsPage;



