import React, { useState } from "react";
import Button from "../components/button.component";
import InputIcon from "../components/input-icon.component";
import Loader from "./../components/loader.component";
import { setCurrentUser } from "../redux/actions/user.actions";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import Message from "../components/message.component";

const RegisterPage = () => {
  const [formValues, setFormValues] = useState({
    username: "",
    contact: "",
    password: "",
    passwordConfirm: "",
  });
  const [formValidity, setFormValidity] = useState({
    username: "",
    contact: "",
    password: "",
    passwordConfirm: "",
  });
  const [error, setError] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const dispatch = useDispatch();

  const history = useHistory();

  const onChangeTextHandler = (event) => {
    setFormValues((currentState) => {
      return { ...currentState, [event.target.name]: event.target.value };
    });
  };

  const isFormValid = () => {
    let fields = {
      username: "",
      contact: "",
      password: "",
      passwordConfirm: "",
    };
    let valid = true;

    if (formValues.username === "") {
      fields.username = "Este campo e obrigatorio";
      valid = false;
    }

    if (formValues.contact === "") {
      fields.contact = "Este campo e obrigatorio";
      valid = false;
    }

    if (formValues.password === "") {
      fields.password = "Este campo e obrigatorio";
      valid = false;
    }

    if (formValues.passwordConfirm === "") {
      fields.passwordConfirm = "Este campo e obrigatorio";
      valid = false;
    }

    if (formValues.password !== formValues.passwordConfirm) {
      fields.passwordConfirm = "As senhas nao sao iguais";
      valid = false;
    }

    setFormValidity(fields);
    return valid;
  };

  const onSubmitHandler = () => {
    const register = async () => {
      try {
        setIsLoading(true);
        setError("");

        const { username, contact, password } = formValues;

        const formData = new FormData();

        formData.append("username", username);
        formData.append("contact", contact);
        formData.append("password", password);

        const response = await fetch(
          `${process.env.REACT_APP_API_URL}/register`,
          {
            method: "POST",
            body: formData,
          }
        );

        const data = await response.json();

        if (data.status === "success") {
          dispatch(setCurrentUser(data.data));

          history.push("/");
        } else {
          setError(data.message);
        }
      } catch (error) {
        console.log("ERROR: " + error);
      } finally {
        setIsLoading(false);
      }
    };

    if (!isFormValid()) return;

    register();
  };
  return (
    <div className="register">
      <div className="container">
        <h1 className="h2 mb-1">Bem-vindo ao Curtela App</h1>
        <p className="p p--justify">
          Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ipsum rerum
          aut tempora sequi aliquam dolores a temporibus, et officia laudantium
          ea aperiam vero laborum est dolor enim laboriosam accusamus rem?
        </p>

        {isLoading && <Loader />}

        {error && <Message message={error} />}
        {!isLoading && (
          <form action="" className="form " onSubmit={onSubmitHandler}>
            <div className="form__group">
              <InputIcon
                type="text"
                name="username"
                placeholder="Nome do usuario: apenas letras e sublinha"
                icon="user"
                value={formValues.username}
                onChange={(e) => onChangeTextHandler(e)}
                pattern="([_a-zA-Z\d\s]{2,})"
                error={formValidity.username}
              />
            </div>
            <div className="form__group">
              <InputIcon
                type="text"
                name="contact"
                placeholder="8XXXXXXXX"
                icon="phone"
                required
                pattern="8[234567][0-9]{7}"
                value={formValues.contact}
                onChange={(e) => onChangeTextHandler(e)}
                error={formValidity.contact}
              />
            </div>
            <div className="form__group">
              <InputIcon
                type="password"
                name="password"
                placeholder="Senha"
                icon="lock"
                required
                value={formValues.password}
                onChange={(e) => onChangeTextHandler(e)}
                error={formValidity.password}
              />
            </div>
            <div className="form__group">
              <InputIcon
                type="password"
                name="passwordConfirm"
                placeholder="Repetir a senha"
                icon="lock"
                required
                value={formValues.passwordConfirm}
                onChange={(e) => onChangeTextHandler(e)}
                error={formValidity.passwordConfirm}
              />
            </div>
            <div className="form__group">
              <Button type="submit" classes={`btn--primary-100 btn--block`}>
                Registar
              </Button>
            </div>
          </form>
        )}
      </div>
    </div>
  );
};

export default RegisterPage;



