import React, { useState } from "react";
import Button from "../components/button.component";
import InputIcon from "../components/input-icon.component";
import Loader from "./../components/loader.component";
import { setCurrentUser } from "../redux/actions/user.actions";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import Message from "../components/message.component";

const ProfileUsername = () => {
  const currentUser = useSelector((state) => state.user.currentUser);
  const [error, setError] = useState("");
  const [formValues, setFormValues] = useState({
    usernameNew: "",
    password: "",
  });
  const [formValidity, setFormValidity] = useState({
    usernameNew: "",
    password: "",
  });
  const [isLoading, setIsLoading] = useState(false);

  const dispatch = useDispatch();

  const onChangeTextHandler = (event) => {
    setFormValues((currentState) => {
      return { ...currentState, [event.target.name]: event.target.value };
    });
  };

  const isFormValid = () => {
    let fields = {
      usernameNew: "",
      password: "",
    };
    let valid = true;

    if (formValues.usernameNew === "") {
      fields.usernameNew = "Este campo e obrigatorio";
      valid = false;
    }

    if (formValues.password === "") {
      fields.password = "Este campo e obrigatorio";
      valid = false;
    }

    setFormValidity(fields);
    return valid;
  };

  const onSubmitHandler = (e) => {
    e.preventDefault();
    const register = async () => {
      try {
        setIsLoading(true);

        const formData = new FormData();

        formData.append("username", formValues.usernameNew);
        formData.append("password", formValues.password);

        const response = await fetch(
          `${process.env.REACT_APP_API_URL}/update-username`,
          {
            method: "POST",
            headers: {
              Authorization: `bearer ${currentUser && currentUser.token}`,
            },
            body: formData,
          }
        );

        const data = await response.json();

        if (data.status === "success") {
          dispatch(setCurrentUser(data.data));
        } else {
          setError(data.message);
        }
      } catch (error) {
        console.log("ERROR: " + error);
      } finally {
        setIsLoading(false);
      }
    };

    if (!isFormValid()) return;

    register();
  };
  return (
    <div className="register">
      <h2 className="h2 mb-2">Alterar Nome do Usuario</h2>

      {isLoading && <Loader />}
      {error && <Message message={error} />}

      {!isLoading && (
        <form className="form " onSubmit={onSubmitHandler}>
          <div className="form__group">
            <InputIcon
              type="text"
              name="name"
              placeholder="Nome actual"
              icon="phone"
              required
              value={currentUser.name}
              disabled
            />
          </div>
          <div className="form__group">
            <InputIcon
              type="text"
              name="usernameNew"
              placeholder="Novo nome"
              icon="phone"
              required
              value={formValues.usernameNew}
              onChange={(e) => onChangeTextHandler(e)}
              error={formValidity.usernameNew}
            />
          </div>
          <div className="form__group">
            <InputIcon
              type="password"
              name="password"
              placeholder="Confirmar com senha"
              icon="lock"
              required
              value={formValues.password}
              onChange={(e) => onChangeTextHandler(e)}
              error={formValidity.password}
            />
          </div>
          <div className="form__group">
            <Button type="submit" classes={`btn--primary-100 btn--block`}>
              Actualizar Nome
            </Button>
          </div>
        </form>
      )}
    </div>
  );
};

export default ProfileUsername;



