import React, { useState } from "react";
import Overlay from "../components/overlay.component";
import Drawer from "./drawer.layouts";
import ProfileMenu from "../components/profile-menu.component";
import { Link } from "react-router-dom";

const Navbar = () => {
  const [drawerIsOpen, setDrawerIsOpen] = useState(false);
  const [profileMenuIsOpen, setProfileMenuIsOpen] = useState(false);

  const openDrawerHandler = () => {
    setDrawerIsOpen(true);
  };

  const closeDrawerHandler = () => {
    setDrawerIsOpen(false);
  };

  const openProfileMenuHandler = () => {
    setProfileMenuIsOpen(true);
  };

  const closeProfileMenuHandler = () => {
    setProfileMenuIsOpen(false);
  };
  return (
    <React.Fragment>
      <Drawer show={drawerIsOpen} closeDrawer={closeDrawerHandler} />
      {drawerIsOpen && <Overlay onClick={closeDrawerHandler} />}

      <nav className="navbar">
        <div className="navbar__container">
          <div className="navbar__left">
            <h1>
              <Link className="navbar__title" to="/">
                Curte la
              </Link>
            </h1>

            <button className="navbar__drawer-btn" onClick={openDrawerHandler}>
              <i className="fa-solid fa-bars navbar__drawer-icon"></i>
            </button>
          </div>
          <div className="navbar__center">
            <h1 className="navbar__title">Curte la</h1>
            <ul className="navbar__menu">
              <li className="navbar__menu-item">
                <Link to="/bars" className="navbar__menu-link">
                  Bares
                </Link>
              </li>

              <li className="navbar__menu-item">
                <Link to="/restaurants" className="navbar__menu-link">
                  Resturantes
                </Link>
              </li>
              <li className="navbar__menu-item">
                <Link to="/bottle-stores" className="navbar__menu-link">
                  Bottle Stores
                </Link>
              </li>
              <li className="navbar__menu-item">
                <Link to="/events" className="navbar__menu-link">
                  Eventos
                </Link>
              </li>
              <li className="navbar__menu-item">
                <Link to="/favorites" className="navbar__menu-link">
                  Favoritos
                </Link>
              </li>
              <li className="navbar__menu-item">
                <Link to="/contacts" className="navbar__menu-link">
                  Contactos
                </Link>
              </li>
            </ul>
          </div>
          <div className="navbar__right">
            <button
              type="button"
              className="btn navbar__profile-btn"
              onClick={openProfileMenuHandler}
            >
              <i className="fa-solid fa-user"></i>
            </button>
            <ProfileMenu
              show={profileMenuIsOpen}
              closeMenu={closeProfileMenuHandler}
            />
            {profileMenuIsOpen && (
              <Overlay onClick={closeProfileMenuHandler} opacity="1" />
            )}
          </div>
        </div>
      </nav>
    </React.Fragment>
  );
};

export default Navbar;



