import React from "react";
import HomeSection from "../sections/home-section.component";

const HomePage = () => {
  return (
    <section className="home page">
      <div className="container">
        <p className="text-lg text-bold">Kme?</p>
        <p className="text-lg text-primary-100 text-bold mb-2">
          Onde vais curtir hoje?
        </p>

        <div className="form__search">
          <input
            type="search"
            className="form__search-input"
            placeholder="Procure um lugar..."
            aria-label="Procure um lugar"
          />
          <i className="fa-solid fa-magnifying-glass form__search-icon"></i>
        </div>

        <HomeSection title="Bares do dia" entity="bars" />
        <HomeSection title="Restaurantes do dia" entity="restaurants" />
        <HomeSection title="Eventos da semana" entity="events" />
      </div>
    </section>
  );
};

export default HomePage;



