import React, { useState } from "react";
import Button from "../components/button.component";
import InputIcon from "../components/input-icon.component";
import Loader from "./../components/loader.component";
import { setCurrentUser } from "../redux/actions/user.actions";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import Message from "../components/message.component";

const ProfilePassword = () => {
  const currentUser = useSelector((state) => state.user.currentUser);
  const [error, setError] = useState("");
  const [formValues, setFormValues] = useState({
    passwordOld: "",
    passwordNew: "",
    passwordConfirm: "",
  });
  const [formValidity, setFormValidity] = useState({
    passwordOld: "",
    passwordNew: "",
    passwordConfirm: "",
  });
  const [isLoading, setIsLoading] = useState(false);

  const dispatch = useDispatch();

  const onChangeTextHandler = (event) => {
    setFormValues((currentState) => {
      return { ...currentState, [event.target.name]: event.target.value };
    });
  };

  const isFormValid = () => {
    let fields = {
      passwordOld: "",
      passwordNew: "",
      passwordConfirm: "",
    };
    let valid = true;

    if (formValues.passwordOld === "") {
      fields.password = "Este campo e obrigatorio";
      valid = false;
    }

    if (formValues.passwordNew === "") {
      fields.passwordNew = "Este campo e obrigatorio";
      valid = false;
    }

    if (formValues.passwordConfirm === "") {
      fields.passwordConfirm = "Este campo e obrigatorio";
      valid = false;
    }

    if (formValues.passwordNew !== formValues.passwordConfirm) {
      fields.passwordConfirm = "As senhas nao sao iguais";
      valid = false;
    }

    setFormValidity(fields);
    return valid;
  };

  const onSubmitHandler = (e) => {
    e.preventDefault();
    const register = async () => {
      try {
        setIsLoading(true);

        const formData = new FormData();

        formData.append("passwordOld", formValues.passwordOld);
        formData.append("passwordNew", formValues.passwordNew);

        const response = await fetch(
          `${process.env.REACT_APP_API_URL}/update-password`,
          {
            method: "POST",
            headers: {
              Authorization: `bearer ${currentUser && currentUser.token}`,
            },
            body: formData,
          }
        );

        const data = await response.json();

        if (data.status === "success") {
          dispatch(setCurrentUser(data.data));
        } else {
          setError(data.message);
        }
      } catch (error) {
        console.log("ERROR: " + error);
      } finally {
        setIsLoading(false);
      }
    };

    if (!isFormValid()) return;

    register();
  };
  return (
    <div className="register">
      <h2 className="h2 mb-2">Alterar Senha</h2>

      {isLoading && <Loader />}
      {error && <Message message={error} />}

      {!isLoading && (
        <form className="form " onSubmit={onSubmitHandler}>
          <div className="form__group">
            <InputIcon
              type="password"
              name="passwordOld"
              placeholder="Antiga Senha"
              icon="lock"
              required
              value={formValues.passwordOld}
              onChange={(e) => onChangeTextHandler(e)}
              error={formValidity.passwordOld}
            />
          </div>
          <div className="form__group">
            <InputIcon
              type="password"
              name="passwordNew"
              placeholder="Nova Senha"
              icon="lock"
              required
              value={formValues.passwordNew}
              onChange={(e) => onChangeTextHandler(e)}
              error={formValidity.passwordNew}
            />
          </div>
          <div className="form__group">
            <InputIcon
              type="password"
              name="passwordConfirm"
              placeholder="Confirmar nova senha"
              icon="lock"
              required
              value={formValues.passwordConfirm}
              onChange={(e) => onChangeTextHandler(e)}
              error={formValidity.passwordConfirm}
            />
          </div>
          <div className="form__group">
            <Button type="submit" classes={`btn--primary-100 btn--block`}>
              Actualizar Senha
            </Button>
          </div>
        </form>
      )}
    </div>
  );
};

export default ProfilePassword;



