import React from "react";
import { Link } from "react-router-dom";

const ActivateAccountPage = () => {
  return (
    <section className="activate-account register">
      <div className="container">
        <h1 className="h2 mb-1">Active sua conta</h1>
        <p className="p p--justify mb-2">
          Para ver todo conteudo e necessario activar a conta. Para tal, siga os
          passos abaixo:
        </p>
        <ol className="activate__steps">
          <li>
            <Link to="/register">Passo 1: Registar uma conta</Link>
          </li>
          <li>
            <Link to="/">Passo 2: Enviar valor X para conta: 12345678</Link>
          </li>
          <li>
            <Link to="/">
              Passo 3: Enviar comprovativo de pagamento para numero de whatsapp
              X
            </Link>
          </li>
          <li>
            <Link to="/">Passo 4: Aguardar a activacao da conta</Link>
          </li>
        </ol>
      </div>
    </section>
  );
};

export default ActivateAccountPage;



