import React from "react";
import { Link } from "react-router-dom";

const Card2 = ({ data, entity, type }) => {
  return (
    <Link
      to={`/details/${entity}/${data.id}`}
      className={`card2 ${type === "horizontal" ? "card2--horizontal" : ""}`}
    >
      <div className={`card2__container`}>
        <div
          className="card2__header"
          style={{
            backgroundImage: `url("${process.env.REACT_APP_ASSET_URL}/${entity}/${data.image_cover}")`,
          }}
        ></div>
        <div className="card2__details">
          <div className="card2__details-item">
            <h2 className="card2__title">{data.name}</h2>
            {entity === "events" ? (
              <div className="label label--primary-100">
                <i className="fa-solid fa-calendar-days label__icon"></i>
                <span>{data.date}</span>
              </div>
            ) : (
              <div className="label label--primary-100">
                <i className="fa-solid fa-star label__icon"></i>
                <span>{data.stars}</span>
              </div>
            )}
          </div>
          <div className="card2__details-item">
            <div className="label label--primary-100">
              <i className="fa-solid fa-location-pin label__icon"></i>
              <span>
                {data.province},{data.neighborhood}
              </span>
            </div>
            <div className="label label--primary-100">
              <i className="fa-solid fa-clock label__icon"></i>
              <span>
                {data.start_time}-{data.end_time}
              </span>
            </div>
          </div>
          {entity === "events" && (
            <div className="card2__details-item">
              <div className="label label--primary-100">
                <i className="fa-solid fa-money-bill label__icon"></i>
                <span>
                  {data.manPrice}(H) - {data.womanPrice}(M)
                </span>
              </div>
            </div>
          )}
          {entity === "events" && (
            <div className="card2__details-item">
              <div className="label label--primary-100">
                <i className="fa-solid fa-money-bill label__icon"></i>
                <span>{data.vipPrice}(VIP)</span>
              </div>
            </div>
          )}
        </div>
      </div>
    </Link>
  );
};

export default Card2;



