import React from "react";

const Comment = ({ comment }) => {
  return (
    <div className="comment ">
      <div className="comment__left">
        <i className="fa-solid fa-user comment__avatar"></i>
      </div>
      <div className="comment__right">
        <div className="comment__header">
          <div className="comment__username">{comment.username}</div>
        </div>
        <div className="comment__status">
          <div className="comment__status-stars">
            {[1, 2, 3, 4, 5].map((item) => {
              return (
                <i
                  key={item}
                  className={`fa-solid fa-star ${
                    item <= comment.stars ? "text-primary-100" : ""
                  }`}
                ></i>
              );
            })}
          </div>
          <div className="comment__status-date">{comment.created_at}</div>
        </div>
        <div className="comment__text p p--justify">{comment.text}</div>
      </div>
    </div>
  );
};

export default Comment;



