import React from "react";
import Navbar from "./layouts/navbar.layouts";
import { Route, Switch, Redirect } from "react-router-dom";
import HomePage from "./pages/home.pages";
import BarsPage from "./pages/bars.pages";
import RestaurantsPage from "./pages/restaurants.pages";
import BottleStores from "./pages/bottlestores.pages";
import EventsPage from "./pages/events.pages";
import DetailsPage from "./pages/details.pages";
import RegisterPage from "./pages/register.pages";
import LoginPage from "./pages/login.pages";
import ActivateAccountPage from "./pages/activate-account.pages";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import { useSelector } from "react-redux";
import ProfilePage from "./pages/profile.pages";
import "./utils/pwa";

const App = () => {
  const currentUser = useSelector((state) => state.user.currentUser);
  return (
    <React.Fragment>
      <Navbar />
      <div className="main">
        <TransitionGroup>
          <CSSTransition
            key={location.pathname}
            classNames="fade"
            timeout={300}
          >
            <Switch>
              <Route exact path="/home">
                <HomePage />
              </Route>
              <Route exact path="/bars">
                <BarsPage />
              </Route>
              <Route exact path="/restaurants">
                <RestaurantsPage />
              </Route>
              <Route exact path="/bottle-stores">
                <BottleStores />
              </Route>
              <Route exact path="/events">
                <EventsPage />
              </Route>
              <Route exact path="/details/:entity/:id">
                <DetailsPage />
              </Route>
              <Route exact path="/register">
                <RegisterPage />
              </Route>
              <Route exact path="/login">
                <LoginPage />
              </Route>
              <Route exact path="/profile">
                <ProfilePage />
              </Route>
              <Route exact path="/activate-account">
                <ActivateAccountPage />
              </Route>
              <Redirect to="/home" />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
      </div>
    </React.Fragment>
  );
};

export default App;



