import React, { useState, useEffect } from "react";
import Card2 from "../components/card2.component";
import useFetch from "../hooks/useFetch.hook";
import Loader from "../components/loader.component";
import { useSelector } from "react-redux";
import Cover from "../components/cover.component";

const BarsPage = () => {
  const [data, setData] = useState([]);
  const [showCover, setShowCover] = useState(true);

  const currentUser = useSelector((state) => state.user.currentUser);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`${process.env.REACT_APP_API_URL}/bars`, {
          method: "GET",
          headers: {
            Authorization: `bearer ${currentUser && currentUser.token}`,
          },
        });

        const result = await response.json();

        if (result.status === "success") {
          setData(result.data);

          if (result.data.length > 5) {
            setShowCover(false);
          }
        }
      } catch (error) {
        console.log(error);
      } finally {
      }
    };

    fetchData();
  }, []);
  return (
    <React.Fragment>
      <Cover show={showCover} />
      <section className="bars">
        <div className="container">
          <div className="flex flex-justify-space-between flex-align-center mb-1">
            <h1>
              Todos <span className="text-primary-100">Bares</span>
            </h1>
            <button className="btn">
              <i className="fa-solid fa-filter"></i>
            </button>
          </div>
          <hr className="mb-2" />

          {data.length === 0 && <Loader />}
          <section className="grid grid-xl-3 grid-lg-2 grid-md-2 grid-sm-1 grid-gap-1">
            {data.length > 0 &&
              data.map((element, index) => {
                return (
                  <div className="grid-item" key={element.id}>
                    <Card2 entity="bars" data={element} />
                  </div>
                );
              })}
          </section>
        </div>
      </section>
    </React.Fragment>
  );
};

export default BarsPage;



